.container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    /* margin: 50px; */
    overflow: hidden;
    /* background: linear-gradient(45deg, rgb(151, 140, 221), #cd5aa9); */
    background: var(--bg-gradient-jet);
    /* background-size: 300px; */
}

.calculator {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60vw;
    height: 97vh;
    flex-direction: column;
    border-radius: 10px;
    /* border: 4px solid slateblue; */
    box-shadow: var(--shadow-3);
    padding: 5px;
    background: var(--bg-gradient-onyx);
}



.calculator-display,
.calculator-result {
    width: 90%;
    height: 20vh;
    padding: 10px;
    font-size: 20px;
    /* text-align: right; */
    outline: none;
    border: none;
    border-radius: 10px;
    border: 5px solid hsl(35, 100%, 68%);
    box-shadow: var(--shadow-1);
    background: var(--bg-gradient-jet);
    overflow: auto;
    color: hsl(35, 100%, 68%);
}

.calculator-display {
    border-bottom: none;
    margin-top: 10px;
}

.calculator-result {
    border-top: none;
}

.calculator-buttons {
    /* background-color: #333; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    flex-direction: column;
    height: 60vh;
    margin-top: 20px;
}

.calculator-row {
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

button {
    width: 33.333%;
    height: 90%;
    font-size: 30px;
    border: none;
    border-radius: 10px;
    background: var(--bg-gradient-yellow-2);
    cursor: pointer;
    margin: 3px;
    transition: all .2s;
    color: hsl(35, 100%, 68%);
}

button:hover {
    /* opacity: .7; */
    scale: 1.04;
    filter: drop-shadow(0 0 5px #fff) drop-shadow(0 0 10px #fff);
    /* box-shadow: var(--shadow-2); */

}

button:active {
    scale: 0.95;
}

@media (max-width:991px) {
    .calculator {
        width: 80vw;
    }
}

@media (max-width:721px) {
    .calculator {
        width: 100vw;
        height: 100vh;
        border: none;

        border-radius: 0px;
    }

    button {
        font-size: 25px;
    }
}